<template>
  <v-container class="pa-7">
    <v-row
      ><v-col class="text-center text-h3 logo-style"><span @click="$router.push('/home/main')">ùïÉùïÇùïÉ</span></v-col></v-row
    >
    <v-row>
      <v-text-field color="black" :rules="emailRules" label="ÏïÑÏù¥Îîî" v-model="member.id"></v-text-field>
    </v-row>
    <v-row>
      <v-text-field color="black" :rules="pwRules" :type="'Password'" class="pa-0" label="ÎπÑÎ∞ÄÎ≤àÌò∏" v-model="member.password"></v-text-field>
    </v-row>
    <v-row class="ma-0" v-if="flag">
      <v-col class="pt-0 pb-0 red--text font-weight-bold">ÏûòÎ™ªÎêú ÏïÑÏù¥Îîî ÎòêÎäî ÎπÑÎ∞ÄÎ≤àÌò∏ÏûÖÎãàÎã§.</v-col>
    </v-row>
    <v-row class="mt-5">
      <v-col align="center" class="login-btn" @click="login">
        <span class="font-weight-bold" dark>Î°úÍ∑∏Ïù∏</span>
      </v-col>
    </v-row>
    <v-row class="mt-5">
      <v-col align="center" @click="join">
        <span class="font-weight-bold grey--text text--darken-3">ÌöåÏõêÍ∞ÄÏûÖ</span>
      </v-col>
    </v-row>
    <v-row>
      <v-col align="center">
        <v-img :src="require('@/assets/photos/kakao_login_medium_narrow.png')" width="200px" @click="loginKakao"></v-img>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import MemberApi from "@/apis/member";
export default {
  name: "Login",
  data: () => ({
    member: {
      id: "",
      password: "",
    },
    emailRules: [(value) => !!value || "ÏïÑÏù¥ÎîîÎ•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî.", (value) => (value || "").length <= 20 || "Max 20 characters"],
    pwRules: [(value) => !!value || "ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî.", (value) => (value || "").length <= 20 || "Max 20 characters"],
    flag: false,
  }),
  methods: {
    async login() {
      try {
        let promise = await MemberApi.login(this.member);
        if (promise.data.result === "success") {
          this.$store.dispatch("saveAuth", {
            id: promise.data.id,
            jwt: promise.data.jwt,
            mno: promise.data.mno,
            gname: promise.data.gname,
            mileagepoint: promise.data.mileage,
          });

          console.log(this.$router);
          this.$router.replace("/home/main");
        } else {
          this.flag = true;
        }
      } catch (error) {
        this.flag = true;
        console.log(error);
      }
    },
    join() {
      this.$router.push("/member/join");
    },
    loginKakao() {
      window.location.replace(
        "https://kauth.kakao.com/oauth/authorize?client_id=7dc281807115ffbb2a7a38f01b635d94&redirect_uri=http://kosa1.iptime.org:50519/member/kakaojoin&response_type=code",
      );
    },
  },
};
</script>

<style scoped>
.login-btn {
  background-color: #c2935f;
}
.logo-style {
  text-shadow: 2px 3px 2px grey;
}
</style>
